import { Box, Button, Card, Container, Divider, Grid, Tab, Tabs, Typography } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { FC, useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import ConfirmDialog from 'src/components/ConfirmDialog'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import { MemberStatus } from 'src/models/member'
import ConferenceTable from './ConferenceTable'
import UserProfile from './UserProfile'
import { Conference } from 'src/models/Conference'

interface ConferencesWithAttend extends Conference {
  isAttend: boolean
}

interface Profile {
  permission: MemberStatus
  level: string
  fee: boolean
  attendance: number
  totalAttendance: number
  createdAt: string
  deletedAt: string | null
  firedAt: string | null
  conferences: ConferencesWithAttend[]
}

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  )
}

const Profile: FC<void> = () => {
  const params = useParams()
  const [profile, setProfile] = useState<Profile>()
  const [isDialog, setIsDialog] = useState<boolean>()
  const [tabIndex, setTabIndex] = useState<number>(0)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const authUser = useAuthUser()

  const club = authUser()?.clubs.filter(c => c.id === parseInt(params.id))[0]

  const handleDelete = () => {
    axios
      .delete(`${process.env.REACT_APP_APIHOST}/club/member?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        if (res.data.message !== 'ok') throw new Error()

        enqueueSnackbar('정상적으로 탈퇴하셨습니다.', { variant: 'success' })
        navigate(`/club/${params.id}`)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response?.data?.message}`, {
          variant: 'error'
        })

        setIsDialog(false)
      })
  }

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabIndex(newValue)
  }

  useEffect(() => {
    if (!club) {
      navigate(`/club/${params.id}`)
    }

    axios
      .get(`${process.env.REACT_APP_APIHOST}/member/status?clubId=${params.id}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Profile>) => {
        setProfile(res.data)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response?.data?.message}`, {
          variant: 'error'
        })
        navigate(`/club/${params.id}`)
      })
  }, [])

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>

      {club && (
        <PageTitleWrapper>
          <Grid container alignItems="center">
            <Grid item>
              <Typography variant="h3" component="h3" gutterBottom>
                {club.name}
              </Typography>
              <Typography variant="subtitle2">
                {authUser().name} 님이 {club.name}에서 활동한 내역입니다
              </Typography>
            </Grid>
          </Grid>
        </PageTitleWrapper>
      )}

      <Container maxWidth="lg">
        {profile && (
          <Card>
            <Tabs value={tabIndex} onChange={handleTabChange} sx={{ margin: 2 }}>
              <Tab label="내 정보" />
              <Tab label="출석" />
            </Tabs>
            <Divider />
            <TabPanel value={tabIndex} index={0}>
              <UserProfile profile={profile} />
            </TabPanel>
            <TabPanel value={tabIndex} index={1}>
              <ConferenceTable conferences={profile.conferences} />
            </TabPanel>
          </Card>
        )}
      </Container>
      <Box textAlign="center" mt={3}>
        <Button
          variant="contained"
          color="error"
          onClick={() => {
            setIsDialog(true)
          }}
        >
          동아리 탈퇴
        </Button>
      </Box>
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        handleSubmit={handleDelete}
      />
    </>
  )
}

export default Profile
