import {
  Box,
  Card,
  CardHeader,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  useMediaQuery,
  useTheme
} from '@mui/material'
import { ChangeEvent, FC, useState } from 'react'
import { semesterName } from 'src/commons/Semester'
import { Conference } from 'src/models/Conference'

interface ConferencesWithAttend extends Conference {
  isAttend: boolean
}

interface ConferenceTableProps {
  conferences: ConferencesWithAttend[]
}

const applyPagination = (items: ConferencesWithAttend[], page: number, limit: number): ConferencesWithAttend[] => {
  return items.slice(page * limit, page * limit + limit)
}

const ConferenceTable: FC<ConferenceTableProps> = ({ conferences }) => {
  const [page, setPage] = useState<number>(0)
  const [limit, setLimit] = useState<number>(5)
  const [selectedSemester, setSelectedSemester] = useState<string>('all')
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage)
  }

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value))
  }

  const sortedConferences = conferences.sort((a, b) => (a.startAt < b.startAt ? 1 : -1))
  const uniqueSemesters = [...new Set(sortedConferences.map(c => c.semester))]
  const filteredConferences = sortedConferences.filter(c => {
    if (selectedSemester === 'all') return true

    return c.semester === (selectedSemester || uniqueSemesters[uniqueSemesters.length - 1])
  })
  const paginatedConferences = applyPagination(filteredConferences, page, limit)

  return (
    <Card elevation={0}>
      <CardHeader
        action={
          <Box width={200} maxWidth={{ xs: 100, sm: 200 }}>
            <FormControl fullWidth variant="outlined">
              <InputLabel>학기</InputLabel>
              <Select
                value={selectedSemester || 'all'}
                onChange={e => setSelectedSemester(e.target.value)}
                label="Semester"
                autoWidth
              >
                <MenuItem key="all" value="all">
                  all
                </MenuItem>
                {uniqueSemesters.map(semester => (
                  <MenuItem key={semester} value={semester}>
                    {semesterName(semester)}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        }
        title="출석 확인"
      />
      <TableContainer>
        <Table>
          {matches && (
            <TableHead>
              <TableRow>
                <TableCell>행사명</TableCell>
                <TableCell>학기</TableCell>
                <TableCell>시작일</TableCell>
                <TableCell>출석 여부</TableCell>
              </TableRow>
            </TableHead>
          )}

          <TableBody>
            {paginatedConferences.map(conference => (
              <TableRow
                key={conference.id}
                sx={{
                  backgroundColor: conference.isAttend ? theme.colors.success.lighter : theme.colors.error.lighter
                }}
              >
                <TableCell sx={{ fontWeight: 'bold' }}>{conference.title}</TableCell>
                {matches && (
                  <>
                    <TableCell>{semesterName(conference.semester)}</TableCell>
                    <TableCell>{conference.startAt}</TableCell>
                    <TableCell>{conference.isAttend ? 'O' : 'X'}</TableCell>
                  </>
                )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={1}>
        <TablePagination
          component="div"
          count={conferences.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
          sx={() =>
            !matches && {
              '.MuiToolbar-gutters': {
                width: '100%',
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center'
              }
            }
          }
        />
      </Box>
    </Card>
  )
}

export default ConferenceTable
