export const semesterName = (semester: string) => {
  switch (semester.substring(0, 4)) {
    case 'SPRI':
      return `${semester.substring(6, 10)}년 1학기`
    case 'SUMM':
      return `${semester.substring(6, 10)}년 여름학기`
    case 'FALL':
      return `${semester.substring(4, 8)}년 2학기`
    case 'WINT':
      return `${semester.substring(6, 10)}년 겨울학기`
    default:
      return `-`
  }
}
